// polyfills
// before polyfills
(function (arr) {
  arr.forEach((item) => {
    if (item.hasOwnProperty('before')) {
      return;
    }
    Object.defineProperty(item, 'before', {
      configurable: true,
      enumerable: true,
      writable: true,
      value: function before() {
        // eslint-disable-next-line prefer-rest-params
        const argArr = Array.prototype.slice.call(arguments);
        const docFrag = document.createDocumentFragment();
        argArr.forEach((argItem) => {
          const isNode = argItem instanceof Node;
          docFrag.appendChild(isNode ? argItem : document.createTextNode(String(argItem)));
        });
        this.parentNode.insertBefore(docFrag, this);
      },
    });
  });
})([Element.prototype, CharacterData.prototype, DocumentType.prototype]);

// forEach polyfill
if (window.NodeList && !NodeList.prototype.forEach) {
  NodeList.prototype.forEach = function (callback, thisArg) {
    // eslint-disable-next-line no-param-reassign
    thisArg = thisArg || window;
    for (let i = 0; i < this.length; i++) {
      callback.call(thisArg, this[i], i, this);
    }
  };
}

const iceCrash = () => {
  const lastScreenElem1 = document.querySelector('.last-screen__ice-top_block');
  const lastScreenElem2 = document.querySelector('.last-screen__ice-bottom_block');
  const lastScreenElem3 = document.querySelector('.last-screen__triangle');
  const lastScreenElem4 = document.querySelector('.last-screen__text');
  const firstScreenIce1 = document.querySelector('.first-screen__ice1');
  const firstScreenIce3 = document.querySelector('.first-screen__ice3');
  const firstScreenIce2 = document.querySelector('.first-screen__ice2');
  const firstScreenIce7 = document.querySelector('.first-screen__ice7');
  const firstScreenIce9 = document.querySelector('.first-screen__ice9');
  const firstScreenIce10 = document.querySelector('.first-screen__ice10');
  const firstScreenIce8 = document.querySelector('.first-screen__ice8');
  const firstScreenIce6 = document.querySelector('.first-screen__ice6');
  const firstScreenIce5 = document.querySelector('.first-screen__ice5');
  const firstScreenIce4 = document.querySelector('.first-screen__ice4');
  const firstScreenBigIce1 = document.querySelector('.first-screen__bigIce1');
  const firstScreenBigIce2 = document.querySelector('.first-screen__bigIce2');

  setTimeout(() => {
    firstScreenIce1.classList.add('active');
    firstScreenIce3.classList.add('active');
    firstScreenIce2.classList.add('active');
    firstScreenIce7.classList.add('active');
    firstScreenIce9.classList.add('active');
    firstScreenIce10.classList.add('active');
    firstScreenIce8.classList.add('active');
    firstScreenIce6.classList.add('active');
    firstScreenIce5.classList.add('active');
  }, 1000);

  setTimeout(() => {
    firstScreenBigIce1.classList.add('active');
    firstScreenBigIce2.classList.add('active');
  }, 1100);

  setTimeout(() => {
    firstScreenIce2.classList.add('active-last');
    firstScreenIce8.classList.add('active-last');
    firstScreenIce6.classList.add('active-last');
    firstScreenIce9.classList.add('active-last');
    firstScreenIce4.classList.add('active-last');
  }, 1100);

  setTimeout(() => {
    lastScreenElem1.classList.add('active');
    lastScreenElem2.classList.add('active');
    lastScreenElem3.classList.add('active');
    lastScreenElem4.classList.add('active');
  }, 1200);
};

iceCrash();

