const toggleMenu = () => {
  const popupMenu = document.querySelector('.popup-menu');
  const burger = document.querySelector('.burger');

  burger.addEventListener('click', () => {
    burger.classList.toggle('active');
    popupMenu.classList.toggle('active');
  });
};

toggleMenu();

const animate = () => {
  gsap.registerPlugin(ScrollTrigger);

  gsap.to('.jar-title', {
    scrollTrigger: {
      trigger: '.new-taste',
      start: 'top center',
    },
    x: 2000,
  });

  gsap.to('.jar-description', {
    scrollTrigger: {
      trigger: '.new-taste',
      start: 'top center',
    },
    x: 2000,
  });

  gsap.to('.new-tast__img', {
    scrollTrigger: {
      trigger: '.new-taste',
      start: 'top center',
    },
    x: -2000,
  });

  gsap.to('.subtitle-top ', {
    scrollTrigger: {
      trigger: '.new-taste',
      start: 'top center',
    },
    x: -2000,
  });

  gsap.to('.title-top', {
    scrollTrigger: {
      trigger: '.new-taste',
      start: 'top center',
    },
    x: -2000,
    delay: 0.5,
  });

  gsap.to('.jar-subtitle', {
    scrollTrigger: {
      trigger: '.new-taste',
      start: 'top center',
    },
    x: 2000,
    delay: 0.5,
  });

  gsap.to('.inside-velo__title', {
    scrollTrigger: {
      trigger: '.inside-velo',
      start: 'top center',
    },
    x: 2000,
  });

  gsap.to('.inside-velo__wrapper', {
    scrollTrigger: {
      trigger: '.inside-velo',
      start: 'top center',
    },
    y: -100,
    opacity: 1,
  });

  gsap.to('.use-velo__title', {
    scrollTrigger: {
      trigger: '.use-velo',
      start: 'top center',
    },
    opacity: 1,
  });

  gsap.to('.use-velo__list', {
    scrollTrigger: {
      trigger: '.use-velo',
      start: 'top center',
    },
    y: -100,
    opacity: 1,
  });
};

animate();

